}

@font-face {
  font-family: "Segoe-UI";
  font-style: italic;
  font-weight: 200;
  src: local("Segoe UI Light Italic"), url("/static/fonts/seguili.ttf") format("truetype");
}

@font-face {
  font-family: "Segoe-UI";
  font-weight: 300;
  src: local("Segoe UI Semilight"), url("/static/fonts/segoeuisl.ttf") format("truetype");
}

@font-face {
  font-family: "Segoe-UI";
  font-style: italic;
  font-weight: 300;
  src: local("Segoe UI Semilight Italic"),
    url("/static/fonts/seguisli.ttf") format("truetype");
}

@font-face {
  font-family: "Segoe-UI";
  font-weight: 400;
  src: local("Segoe UI"), url("/static/fonts/segoeui.ttf") format("truetype");
}

@font-face {
  font-family: "Segoe-UI";
  font-style: italic;
  font-weight: 400;
  src: local("Segoe UI Italic"), url("/static/fonts/segoeuii.ttf") format("truetype");
}

@font-face {
  font-family: "Segoe-UI";
  font-weight: 500;
  src: local("Segoe UI Semibold"), url("/static/fonts/seguisb.ttf") format("truetype");
}

@font-face {
  font-family: "Segoe-UI";
  font-style: italic;
  font-weight: 500;
  src: local("Segoe UI Semibold Italic"),
    url("/static/fonts/seguisbi.ttf") format("truetype");
}

@font-face {
  font-family: "Segoe-UI";
  font-weight: 700;
  src: local("Segoe UI Bold"), url("/static/fonts/segoeuib.ttf") format("truetype");
}

@font-face {
  font-family: "Segoe-UI";
  font-style: italic;
  font-weight: 700;
  src: local("Segoe UI Bold Italic"), url("/static/fonts/segoeuiz.ttf") format("truetype");
}

html {
  scroll-behavior: smooth;
  scroll-padding-top: 50px;
}

body {
  font-family: "Segoe-UI";
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

h1:hover,
h2:hover,
h3:hover,
h4:hover,
h5:hover,
h6:hover,
.page-navbar .navbar-brand:hover,
.dev-icons .list-inline-item i:hover,
legend > h2:hover,
.card-info .text-right:hover,
.card-info .card-title:hover,
.card-info .card-subtitle:hover,
.read-more-button:hover,
.page-footer .go-to-top:hover,
.brand:hover {
  color: #bd5d38 !important;
}

.page-navbar .navbar-nav > li > a:hover,
.social-icons a:hover,
.dev-icons > i:hover,
.dev-title-image:hover {
  background-color: #bd5d38;
}

.page-container {
  margin: 0px;
  padding: 0px;
  color: #676f75;
  background-color: #f8f8f8;
}

/* navbar */
.navbar-brand {
  margin: auto 20px;
  padding-right: 20px;
}

.page-navbar {
  background-color: #495057;
}

.page-navbar .navbar-nav > li > a:hover {
  border-radius: 5px;
}

/* Brand */
.brand {
  text-decoration: none !important;
  color: #6c757d;
}

.brand > .brand-title {
  font-style: italic;
  font-weight: bold;
  text-transform: capitalize;
  font-size: 1.25rem;
}

.brand > .brand-subtitle {
  font-weight: lighter;
  text-transform: capitalize;
  font-size: xx-small;
  margin-left: 2px;
}

.speciality {
  margin: 0px;
  padding: 0px;
  list-style-type: none !important;
}

/* Page sections */
.page-section {
  margin-top: 56px;
  position: relative;
}
.page-section > .container .col-lg-12 {
  margin-top: 1.5rem;
  margin-bottom: 1.5rem;
  text-align: center;
}

/* Page image */
.page-section > #page-image,
.headline-text {
  height: 600px;
}

.carousel-inner img {
  width: 100%;
  height: 600px;
  object-fit: cover;
}

/* Headline */
.headline-text {
  margin: 0px;
  padding: 0px;
  background-color: #292929;
  /* background-color: #747474; */
  color: #bd5d38;
  text-shadow: 8px 8px 15px #e6e4e4;
  /* color: #e6e4e4;
  text-shadow: 5px 5px 15px #bd5d38; */
  text-align: center;
  font-size: 25rem;
}

.headline-text i {
  display: inline-block;
  margin: 40px;
}

/* Social icons */
.social-icons {
  margin-top: 10px;
}
.social-icons a {
  display: inline-block;
  height: 3.5rem;
  width: 3.5rem;
  background-color: #495057;
  color: #fff !important;
  border-radius: 100%;
  text-align: center;
  font-size: 1.5rem;
  line-height: 3.5rem;
  margin-right: 1rem;
}

.social-icons a:last-child {
  margin-right: 0;
}

/* skills icon */
.dev-icons {
  font-size: 2rem;
}

.dev-title-image {
  background-image: url("data:image/gif;base64,R0lGODlhAQABAIAAAHd3dwAAACH5BAAAAAAALAAAAAABAAEAAAICRAEAOw==");
  background-size: contain;
  width: 140px;
  height: 140px;
  border-radius: 50%;
  position: relative;
  margin: 0px auto;
}

.dev-title-image:hover {
  background-image: none;
}

.dev-title-image-text {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  text-align: center;
  z-index: 2;
  color: #fff;
  font-weight: bold;
}

.dev-text {
  margin-top: 10px;
  text-align: justify;
}

.dev-icons > i {
  display: inline-block;
  height: 3.5rem;
  width: 3.5rem;
  background-color: #495057;
  color: #fff !important;
  border-radius: 100%;
  text-align: center;
  font-size: 3rem;
  line-height: 3.5rem;
  margin-right: 1rem;
}

/* Fieldset and legend */
fieldset {
  border: 2px solid #ccc;
  padding: 1em;
  /* box-shadow: 0.5px 0.5px 0.5px 0.5px #ccc; */
}

legend > h2 {
  background-color: #f8f8f8;
}

/* Info card */
.card-info {
  margin-top: 20px;
  padding: 5px;
  border-bottom: 0.5px ridge #ddd;
}

.card-info .card-subtitle {
  color: #6c757d;
}

.card-info .card-title,
.card-info .card-subtitle {
  text-transform: uppercase;
}

.card-text {
  text-align: justify;
}
.card-info .card-text p {
  text-align: justify;
}

/* icons */
.page-icon {
  position: absolute;
  right: 80px;
  height: 80px;
  /* transform: translate(-50%, -50%); */
}

/* parallax */
.parallax-edu {
  /* The image used */
  background-image: url("/static/images/graduate.png");
  background-color: rgba(0, 0, 0, 0.1);

  /* Set a specific height */
  height: 1100px;
  width: 100%;

  /* Create the parallax scrolling effect */
  background-attachment: fixed;
  background-position: bottom right;
  background-repeat: no-repeat;
  background-size: cover;
  -webkit-background-size: cover;
  -moz-background-size: cover;
  -o-background-size: cover;
  filter: blur(1px);
  -webkit-filter: blur(1px);
  color: #bd5d38;
}

.parallax-edu-content {
  background-color: rgb(239, 239, 239); /* Fallback color */
  background-color: rgba(239, 239, 239, 0.9); /* Black w/opacity/see-through */
  font-weight: bold;
  position: absolute !important;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 2;
  text-align: center;
}

.parallax-edu-content legend > h2 {
  background-color: transparent;
}

.parallax-exp {
  position: relative;
}
.parallax-exp > img {
  height: 400px;
  width: 100%;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;

  margin: 0;
  position: absolute;
  top: 50%;
  left: 5px;
  -ms-transform: translateY(-50%);
  transform: translateY(-50%);
}

/* Awards */
.award-list li {
  margin-top: 15px;
  padding-bottom: 5px;
  border-bottom: 0.5px ridge rgba(221, 221, 221, 0.7); /*#ddd */
}
.list .fa-ul {
  margin-left: unset !important;
}

.list .fa-ul .fa-li {
  margin-left: 40px !important;
}

.award-list li h6,
.award-list li p {
  margin: 0px;
  padding: 0px;
}
.award-issuer {
  font-size: 12px;
}

.award-description {
  font-size: 14px;
}

/* Orgs link and logos */
.org-link {
  text-decoration: none !important;
}

.org-logo {
  margin-left: 15px;
  height: 40px;
}

/* Address */
#Education .card-info h5 {
  margin-bottom: 0px;
  padding-bottom: 0px;
}

address {
  margin: 0px;
  padding: 0px;
  padding-bottom: 10px;
  font-size: 14px;
  font-weight: normal;
  font-style: italic;
  text-transform: none;
}

/* Read more button */
.read-more-button {
  color: #007bff !important;
  cursor: pointer;
  text-align: center;
  font-style: italic;
  font-weight: normal;
  font-size: 14px;
  margin-left: 5px;
  display: inline-block;
}

/* footer */
.page-footer {
  color: #6c757d;
  height: 115px;
  width: 100%;
  margin: 0px;
  margin-top: 10px;
  padding: 10px;
  background-color: #efefef;
  text-align: center;

  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
  flex-wrap: wrap;
  margin-right: -15px;
}

.page-footer .go-to-top {
  text-decoration: none !important;
  color: #6c757d;
  font-size: small;
}

.page-footer .sides,
page-footer .mid {
  position: relative;
  width: 100%;
  min-height: 1px;
  padding-right: 15px;
  padding-left: 15px;
}

.page-footer .sides {
  -webkit-box-flex: 0;
  -ms-flex: 0 0 16.666667%;
  flex: 0 0 16.666667%;
  max-width: 16.666667%;
}

.page-footer .mid {
  -webkit-box-flex: 0;
  -ms-flex: 0 0 66.666667%;
  flex: 0 0 66.666667%;
  max-width: 66.666667%;
}

@media only screen and (max-width: 992px) {
  .parallax-exp {
    display: none;
  }

  #Experience fieldset > .row > .col-8 {
    width: 100% !important;
    flex: none !important;
    max-width: 100% !important;
  }

  .parallax-edu {
    height: 1250px;
  }
}

@media only screen and (max-width: 768px) {
  .page-section > #page-image,
  .headline-text {
    height: 400px;
  }

  .headline-text {
    font-size: 15rem;
  }

  .carousel-inner img {
    width: 100%;
    height: 400px;
    object-fit: cover;
  }

  .parallax-exp {
    display: none;
  }

  #Experience fieldset > .row > .col-8 {
    width: 100% !important;
    flex: none !important;
    max-width: 100% !important;
  }

  .parallax-edu {
    height: 1450px;
  }

  .card-flex > .col-4,
  .card-flex > card-flex-div {
    display: block;
    flex: none !important;
    max-width: 100% !important;
    margin-top: 20px;
    padding-bottom: 15px;
    border-bottom: 0.5px ridge rgba(221, 221, 221, 0.7); /* #ddd */
  }

  .page-icon {
    right: 60px;
    height: 60px;
  }

  #ExtraActivities .page-icon {
    transform: translate(30%, 60%);
  }

  .list .fa-ul .fa-li {
    margin-left: 20px !important;
  }
}

@media only screen and (max-width: 600px) {
  .page-section > #page-image,
  .headline-text {
    height: 300px;
  }

  .headline-text {
    font-size: 10rem;
  }

  .carousel-inner img {
    width: 100%;
    height: 300px;
    object-fit: cover;
  }

  .parallax-edu {
    height: 1550px;
  }

  .page-icon {
    right: 55px;
    height: 55px;
  }

  #ExtraActivities .page-icon {
    transform: translate(30%, 80%);
  }

  .list .fa-ul .fa-li {
    margin-left: 15px !important;
  }

  /* footer */
  .page-footer {
    height: 160px;
  }
  .page-footer .sides,
  page-footer .mid {
    position: relative;
    width: 100%;
    min-height: 1px;
    padding-right: 15px;
    padding-left: 15px;
  }

  .page-footer .sides {
    margin-top: 10px;
    -webkit-box-flex: 0;
    -ms-flex: unset;
    flex: unset;
    width: 100%;
    max-width: 100%;
  }

  .page-footer .mid {
    width: 100%;
    -webkit-box-flex: 0;
    -ms-flex: unset;
    flex: unset;
    max-width: 100%;
  }
}

@media only screen and (max-width: 480px) {
  .parallax-edu {
    height: 1650px;
  }

  .social-icons a {
    display: inline-block;
    height: 2rem;
    width: 2rem;
    line-height: 2rem;
    font-size: 1rem;
  }

  .page-icon {
    right: 40px;
    height: 40px;
  }

  #ExtraActivities .page-icon {
    transform: translate(30%, 140%);
  }

  .list .fa-ul .fa-li {
    margin-left: 6px !important;
    font-size: small;
  }
}

@media only screen and (max-width: 365px) {
  .page-section > #page-image,
  .headline-text {
    height: 250px;
  }

  .headline-text {
    font-size: 8rem;
  }

  .carousel-inner img {
    width: 100%;
    height: 250px;
    object-fit: cover;
  }

  .parallax-edu {
    height: 1750px;
  }
}

@media only screen and (max-width: 355px) {
  .parallax-edu {
    height: 1900px;
  }
  .page-section {
    margin-top: 96px;
  }
  .page-icon {
    display: none;
  }
}

@media only screen and (max-width: 302px) {
  .parallax-edu {
    height: 2000px;
  }
}

@media only screen and (max-width: 285px) {
  .parallax-edu {
    height: 2300px;
  }
}
